ul {
  padding: 0;
  margin: 0;
}

.container {
  width: 100%;
}
.footer-title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 1.36;
  text-align: center;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.color-accent {
  color: #ff6b09;
}
.footer-registration {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  text-align: center;
  letter-spacing: 0.08em;
  color: #ffffff;
}
.modal-form-wrapper {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.btn-footer {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 5px;

  min-width: 260px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.21;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: #ffffff;
  background: #ff6b09;
  border: none;
}
.copyright {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 2;
  display: flex;
  align-items: center;
  color: #b9b9b9;
}
.contact-form-input {
  width: 280px;
  min-height: 48px;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  background: transparent;
  padding-left: 48px;
}
.contact-list-item {
  .contact-tel {
    text-decoration: none;
    display: flex;
    justify-content: center;
  }
}
.footer-general {
  padding-top: 50px;
  padding-bottom: 50px;
  background-image: linear-gradient(
      to bottom,
      rgba(17, 21, 24, 0.8),
      rgba(17, 21, 24, 0.8)
    ),
    url(../images/footer-mobile.jpg);
}
.footer-title {
  font-size: 14px;
  line-height: 1.36;
  padding-bottom: 30px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}
.footer-registration {
  padding-bottom: 30px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  font-size: 12px;
  line-height: 1.25;
}
.contact-form-input {
  display: block;
  justify-content: center;
  margin-left: 20px;
  margin-right: 20px;

  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.08em;

  color: #ffffff;
}
.contact-form-input::placeholder {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  display: block;
  align-items: center;
  letter-spacing: 0.08em;

  color: #ffffff;
}
.copyright-box {
  padding-top: 22px;
  padding-bottom: 21px;
  padding-left: 52px;
  padding-right: 51px;
  background: #111518;
}
.contact-tel {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.08em;

  color: #111518;
}
.contact-list {
  list-style: none;
  display: block;
  justify-content: center;
  text-align: center;
  margin-left: 60px;
  margin-right: 60px;
}
.contact-box {
  padding-top: 30px;
  padding-bottom: 30px;
}
.contact-list-item:not(:last-child) {
  margin-bottom: 21px;
}
.contact-list-item {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
  .footer-general {
    padding-bottom: 85px;
    background-image: linear-gradient(
        to bottom,
        rgba(17, 21, 24, 0.8),
        rgba(17, 21, 24, 0.8)
      ),
      url(../images/footer-tablet.jpg);
  }

  .footer-title {
    padding-left: 40px;
    padding-right: 40px;
    font-size: 26px;
    line-height: 1.3;
  }
  .footer-registration {
    padding-bottom: 40px;
    padding-left: 158px;
    padding-right: 158px;
    font-size: 14px;
    line-height: 1.21;
  }

  .contact-form-input {
    width: 390px;
  }
  .contact-list {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    margin: 0 auto;
  }
  .contact-box {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .contact-list-item {
    display: flex;
    justify-content: center;
    align-items: center;
    .logo {
      margin-left: 40px;
    }
    &:not(:last-child) {
      margin-bottom: 0;
    }
    .icon-phone {
      margin-right: 10px;
    }
  }

  .contact-list-item {
    .contact-tel {
      margin-left: 237px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      padding-right: 24px;
    }
  }
  .contact-list-item {
    .sign-instagram {
      position: relative;
      padding-left: 24px;
    }
  }
  .contact-list-item {
    .sign-instagram {
      &::before {
        display: block;
        content: '';
        position: absolute;
        margin-right: 24px;
        width: 0px;
        height: 40px;
        border: 1px solid #b9b9b9;
        left: 0;
      }
    }
  }
  .contact-tel {
    font-size: 24px;
    line-height: 1.2;
  }
}
@media screen and (min-width: 1314px) {
  .container {
    min-width: 1314px;

    .footer-general {
      padding-top: 100px;
      padding-bottom: 88px;
      background-image: linear-gradient(
          to bottom,
          rgba(17, 21, 24, 0.8),
          rgba(17, 21, 24, 0.8)
        ),
        url(../images/footer-desktop.jpg);
    }
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
  }
  .footer-title {
    padding-bottom: 50px;
    font-size: 46px;
    line-height: 1.3;
  }
  .footer-registration {
    padding-bottom: 56px;
    font-size: 30px;
    line-height: 1.23;
  }
  .contact-form-input {
    width: 478px;
    min-height: 70px;
  }
  .btn-footer {
    padding: 27px 25px;
    min-width: 354px;

    font-weight: 800;
    font-size: 14px;
    line-height: 1.21;
  }
  .contact-list-item {
    .logo {
      margin-left: 183px;
    }
  }
  .contact-list-item {
    .contact-tel {
      margin-left: 290px;
    }
  }
  .contact-tel {
    font-size: 30px;
    line-height: 1.23;
  }
  .contact-box {
    padding-top: 52px;
    padding-bottom: 52px;
  }
}
